import { client } from "./sanity";
import { v4 as uuidv4 } from "uuid";

export async function addUser({ id, nickname, name, email, password, image }) {
  return client.createIfNotExists({
    _id: id,
    _type: "user",
    nickname,
    name,
    email,
    password,
    image,
    bookmarks: [],
  });
}

export async function addBookmark({
  id,
  videoId,
  thumbnails,
  title,
  channelTitle,
  publishedAt,
}) {
  return client
    .patch(id)
    .setIfMissing({ bookmarks: [] })
    .append("bookmarks", [
      {
        _id: videoId,
        videoId,
        thumbnails,
        title,
        channelTitle,
        publishedAt,
        _type: "object",
      },
    ])
    .commit({ autoGenerateArrayKeys: true })
    .then(() => {
      alert(" .");
    });
}

export async function removeBookmark({ id, videoId }) {
  return client
    .patch(id)
    .unset([`bookmarks[_id=="${videoId}"]`])
    .commit()
    .then(() => {
      alert(" .");
    });
}

export async function getBookmarks({ email }) {
  return client.fetch(
    `*[_type == "user" && email == "${email}"][0]{
      "bookmarks" : bookmarks[]}
    `
  );
}

export async function bookmarkCheck({
  email,
  id,
  videoId,
  thumbnails,
  title,
  channelTitle,
  publishedAt,
}) {
  return client
    .fetch(
      `*[_type == "user" && email == "${email}"][0]{
      "bookmarks" : "${videoId}" in bookmarks[].videoId}
    `
    )
    .then((res) =>
      res.bookmarks === false || res.bookmarks === null
        ? addBookmark({
            id,
            videoId,
            thumbnails,
            title,
            channelTitle,
            publishedAt,
          })
        : window.confirm("  . ?")
        ? removeBookmark({
            id,
            videoId,
          })
        : null
    );
}

export async function emailSignUpCheck({ email, password, name, nickname }) {
  return client
    .fetch(`*[_type == "user" && email == "${email}"][0]`)
    .then((res) =>
      res !== null
        ? alert(" .")
        : addUser({
            id: uuidv4(),
            nickname,
            name,
            email,
            password,
            image:
              "http://www.fao.org/fileadmin/templates/experts-feed-safety/images/profile-img03.jpg",
          })
    );
}

export async function getEmailLogin({ email, setUser, navigate }) {
  return client
    .fetch(`*[_type == "user" && email == "${email}"][0]`)
    .then((res) => {
      let userObject = {
        id: res._id,
        name: res.name,
        image: res.image,
        email: res.email,
        nickname: res.email.split("@")[0],
      };
      setUser(userObject);
      localStorage.setItem("userInfo", JSON.stringify(userObject));
      navigate("/");
    });
}

export async function emailLogin({ email, password, setUser, navigate }) {
  return client
    .fetch(
      `*[_type == "user" && email == "${email}"][0]{"password" : password == "${password}"}`
    )
    .then((res) =>
      res.password === false
        ? alert(" ")
        : getEmailLogin({ email, setUser, navigate })
    );
}
